.Footer-container{
    position: relative;
}
.Footer-container>hr{
    border: 1px solid var(--lightgray);

}
.footer{
    padding: 3rem 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4rem;
}
.footer>img{
    width: 2rem;
    height: 2rem;
    cursor: pointer;
}
.logo-f{
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0rem 0rem 3rem 0rem;
}
.logo-f>img{
    width: 10rem;
    height: 4rem;
}
.blur-f-2{
    width: 35rem;
    height: 6rem;
    bottom: 0;
    left: 4rem;
    background:red ;
    filter: blur(150px);
}
.blur-f-1{
    width: 35rem;
    height: 6rem;
    bottom: 0;
    right: 4rem;
    background:orangered ;
    filter: blur(150px);
}