.Testimonials{
    display: flex;
    gap: 1rem;
    padding: 0 2rem;
}

.left-t{
    display: flex;
    flex: 1 1;
    gap: 2rem;
    flex-direction: column;
    text-transform: uppercase;
    color: white;
}
.left-t>:nth-child(1){
    color: var(--orange);
    font-weight: bold;
}
.left-t>:nth-child(2){
    font-size: 3rem;
    font-weight: bold;
}
.left-t>:nth-child(3){
    font-size: 3rem;
    font-weight: bold;
}

.left-t>:nth-child(4){
    text-transform: none;
    text-align: justify;
    letter-spacing: 2px;
    line-height: 40px;
    width: 40rem;
}

.right-t{
    flex:1 1;
    position: relative;
}

.right-t>img{
    position: absolute;
    object-fit: cover;
    width: 17rem;
    height: 20rem;
    top: 2rem;
    right: 8rem;
}

.right-t>:nth-child(1){
    position: absolute;
    width: 17rem;
    height: 20rem;
    border: 3px solid var(--orange);
    background-color: transparent; 
    top: 0.9rem;
    right: 9rem; 
}
.right-t>:nth-child(2){
    position: absolute;
    width: 17rem;
    height: 19rem;
    background: var(--planCard); 
    top: 4rem;
    right: 7rem; 
}

.arr{
    display: flex;
    gap: 1rem;
    position: absolute;
    bottom: 1rem;
    left: 3rem;
}
.arr>img{
    width: 1.5rem;
}
.arr>img:hover{
    cursor: pointer;
}

@media screen and (max-width:768px) {
    .Testimonials{
        flex-direction: column;
    }
    .left-t>:nth-child(2),.left-t>:nth-child(3){
        font-size: xx-large;
    }
    .right-t{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 2rem;
    }
    .right-t>div{
        position: relative;
        display: none;
    }
    .left-t>:nth-child(4){
        text-transform: none;
        text-align: justify;
        letter-spacing: 1px;
        line-height: 25px;
        width: 15rem;
        font-size:medium;
    }
    .right-t>img{
        top: 0;
        right: 0;
        position: relative;
        align-self: center;
    }
    .right-t>:last-child{
        display: block;
        bottom: 0;
        left: 0;
    }
}