.Join{
    display: flex;
    padding: 0 2rem;
    gap: 10rem;
}
.left-j{
    flex: 1 1;
    display: flex;
    flex-direction: column;
    color: white;
    font-size: 3rem;
    text-transform: uppercase;
    gap :1rem;
    font-weight: bold;
    position: relative;
}

.left-j>hr{
    position: absolute;
    width: 10.5rem;
    border: 3px solid var(--orange);
    border-radius: 50%;
    margin: -10px 0 ;
}
.right-j{
    flex: 1 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.email-container{
    display: flex;
    gap: 3rem;
    background-color: gray;
    padding: 1rem 2rem;
}
.email-container>input{
    background-color: transparent;
    border: none;
    outline: none;
    color: var(--lightgray);
}
::placeholder{
    color: var(--lightgray); 
}
btn-j{
    background-color: var(--orange);
    color: white;
}
@media screen and (max-width:768px) {
    .Join{
        flex-direction: column;
        gap: 1rem;

    }
    .left-j{
        font-size: 1.4rem;
        flex-direction: column;
    }
    .right-j{
        padding: 2rem;
    }
}