.header{
    display: flex;
    justify-content: space-between;
}
.logo{
    width: 10rem;
    height: 3rem;
}
.header-menu{
    display: flex;
    list-style: none;
    gap: 2rem;
    color: white;
}
.header-menu>li:hover{
    cursor: pointer;
    color: var(--orange);
}

@media screen and (max-width:768px) {
    .header>:nth-child(2){
        position: fixed;
        right: 2rem;
        z-index: 99;
    }
    .header-menu{
        flex-direction: column;
        background-color: var(--appColor);
        padding: 1rem;
        gap: 1rem;
        font-size: small;
        align-items: center;
        justify-content: center;
        border-radius: 10px;
    }
    .header-menu>:nth-child(1){
        position: absolute;
        top: 5px;
        right: 10px;
        font-size: large;
        color: rgb(247, 179, 133);
    }
    .header-menu>:nth-child(1):hover{
        color: rgb(247, 179, 133);
    }
}